
* {
    margin: 0; 
    padding: 0;
}
html,
body {
    font: 
        12px/1.5 
        Tahoma, Helvetica, Arial, "Microsoft YaHei", "Hiragino GB", Sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
ul,ol {
	list-style-type: none;
}
a {
	text-decoration: none;
}
a:link {
	color: #000;
}
a:hover {
	color: #0000A0;
}
a:visited {
	text-decoration: underline;
	color: #00f;
}
a img,
div img {
	vertical-align: top;
	/* display: block; */
}












































































































































































































































































/* 通用 */
.iconfont {
  cursor: pointer;
}
/* 结构 */
#home {
  margin: 0 20px;
  min-width: 1280px;
}
#cart {
  margin-top: 60px;
}
/* 1.购物车标题 */
#checkout-title {
    position: relative;
    text-align: center;
    line-height: 2;
}
#checkout-title:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border: .5px solid #ccc;
}
#checkout-title h2 {
    position: relative;    /*为了使用z-index*/
    display: inline;
    padding: 0 1em;
    font-size: 20px;
    background: #fff;
    z-index: 1;
}
/* 2-1.购物车列表-标题 */
.cart-item-header {
    font-size: 0;
    letter-spacing: -3px;
}
.cart-item-header li {
    display: inline-block;
    height: 54px;
    line-height: 54px;
    width: 12%;
    font-size: 18px;
    color: #fff;
    background: #cd1;
}
.cart-item-header li:first-child {
    width: 52%;
    text-align: center;
}
/* 2-2. 购物车列表--内容 */
.cart-item-list {
    margin: 30px 0;
}
.cart-item-list > ul > li {
    padding: 20px 0;
    font-size: 0;
    letter-spacing: -3px;
    border-bottom: 1px dashed #bbb;
}
.cart-tab1,
.cart-tab2,
.cart-tab3,
.cart-tab4,
.cart-tab5 {
    display: inline-block;
    width: 12%;
    font-size: 14px;
    letter-spacing: 0;
    vertical-align: middle;
}
.cart-tab1 {
    width: 52%;
}
.item-pic {
    height: 86px;
    width: 112px;
    margin: 0 20px;
    vertical-align: middle;
}
/* 购物项信息 */
.item-info {
    display: inline-block;
    vertical-align: top;
}
.item-title {
    height: 80px;
    font-weight: 700;
}
.item-include {
    position: relative;
    width: 150px;
}
.item-include dt {
    float: left;
}
.item-include dd {
    margin-left: 50px;
}
/* 购物项数量 */
.item-quantity input {
    width: 30px;
    text-align: center;
}
/* 3.购物车结算*/
.item-all-check {
    float: left;
    height: 40px;
    font-size: 16px;
}
.checkout-account {
    float: right;
    height: 40px;
    font-size: 20px;
}
.checkout-account > div {
    float: left;
}
.checkout-btn a {
    margin-left: 30px;
    padding: 10px 60px;
    font-size: 18px;
    font-weight: 700;
    border: 1px solid #000;
    border-radius: 5px;
    color: #fefcff;
    background: #e5c;
}








































/* 遮罩 注意top和left*/
#shade[data-v-82eb2474] {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	opacity: .5;
	background: #bbb;
	z-index: 99;
}
/* 对话框 注意top和left*/
#dialog-delete-box[data-v-82eb2474] {
	position: fixed;
	width: 400px;
	height: 250px;
	top: 50%;
	left: 50%;
	margin-left: -200px;
	margin-top: -125px;
	background: #fefcff;
	z-index:999;
}
.box-top[data-v-82eb2474] {
	height: 40px;
	margin-bottom: 40px;
}
.box-top span[data-v-82eb2474] {
	position: absolute;
	right: 5px;
	top: 0;
	font-size: 30px;
	cursor: pointer;
}
.box-content[data-v-82eb2474] {
	text-align: center;
	font-size: 20px;
}
.btn-wrap[data-v-82eb2474] {
	margin: 50px 0 30px 0;
}
.btn-wrap button[data-v-82eb2474] {
	padding: 10px 50px;
	font-size: 18px;
	cursor: pointer;
}
.btn-confirm[data-v-82eb2474] {
	margin-right: 5px;
	border: 1px solid #e56717;
	color: #faa;
	background: #fff;
}
.btn-cancel[data-v-82eb2474] {
	margin-left: 5px;
	border: 1px solid #faa;
	background: #e56717;
	color: #000;
}


























































/* 结构*/
#orderProcedure[data-v-218beb82] {
	min-width:1280px;
}	
/* 1. 订单流程*/
.procedure-title[data-v-218beb82] {
	/* 清除浮动，不用margin-bottom的原因是:after的圆点会被修剪*/
	overflow: hidden;
	padding-bottom: 40px;
}
.procedure-title > div[data-v-218beb82] {
	float: left;
	position: relative;
	height: 40px;
	width: 25%;
	padding: 20px 0 10px 0;
	border-bottom: 2px solid #ccc;
	text-align: center;
	font-size:20px;
    color: #ccc;
}
.procedure-title > div[data-v-218beb82]:after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 50%;
	margin: 0 0 -7px -7px;	
	width: 14px;
	height: 14px;
	background: #ccc;
	border-radius: 7px;
}
.procedure-title > .current-procedure[data-v-218beb82] {
	color: #fa0;
	border-color:#fa0;
}
.procedure-title > .current-procedure[data-v-218beb82]:after {
	background: #fa0;
}	
/* 2. 下一步按钮*/
.next-step-btn[data-v-218beb82] {
	display: inline-block;
	float: right;
	margin: 10px 40px 0 0;
	padding: 5px 10px;
	border: 1px solid #000;
	border-radius: 5px;
	font-size: 18px;
	font-weight: 700;
	background: #fa0;
}







































































































































/* 通用-- part2和part3的标题抬头*/
.address-title[data-v-47d164ca],
.shipping-title[data-v-47d164ca] {
	position: relative;  /* 水平线的基准*/
	margin-bottom: 30px;
	text-align: center;
}
.address-title span[data-v-47d164ca],
.shipping-title span[data-v-47d164ca] {
	position: relative;
	display: inline;
	padding: 0 20px;
	background: #fefcff;
	z-index: 99;
}
.address-title[data-v-47d164ca]:before,
.shipping-title[data-v-47d164ca]:before {
	content: "";
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	border: .5px dotted #ccc;
}
.iconfont[data-v-47d164ca] {
	cursor: pointer;
}
.selected[data-v-47d164ca] {
	border:2px solid #fa0!important;
}
/* 1. 地址卡片列表*/
#address-list[data-v-47d164ca] {
	margin-bottom:40px;
}
/* 1.2 地址卡片*/
#address-list ul[data-v-47d164ca] {
	margin-right: -1%;  /*拉长最右卡片，隐藏padding-right的空白*/
	font-size: 0;
	letter-spacing: -3px;
}
.adr-list-card[data-v-47d164ca],
.add-address[data-v-47d164ca] {
	display: inline-block;
	position: relative;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	width: 24%;
	height: 180px;
	margin: 0 1% 20px 0;
	padding: 10px;
    /* inline元素默认是baseline对齐，一个 inline-block 如果其中有内容，
    其baseline 就是最后一行文字的baseline，否则就是其margin下边缘。 */
	vertical-align: top;
	border: 1px dashed #bbb;
	font: 16px/2 "Microsoft YaHei";
	letter-spacing: 1px;
}
.add-address[data-v-47d164ca] {
	text-align: center;
	line-height: 2;
}
.add-address .icon-add[data-v-47d164ca]{
	font-size: 56px;
}
.adr-list-card[data-v-47d164ca]:hover {
	border: 2px solid #fa0;
}
.default-address[data-v-47d164ca] {
	font: 14px/2 "Hiragino GB";
	font-weight: 700;
	color: #c60;
}
/* 1.3 鼠标进入地址卡片的设置项*/
.set-default-address[data-v-47d164ca] {
	font: 12px/2 "Hiragino GB";
	cursor: pointer;
}
.icon-edit[data-v-47d164ca] {
	position: absolute;
	top: 0;
	right: 0;
	font-size: 20px;
}
.icon-delete[data-v-47d164ca] {
	position: absolute;
	bottom: 0;
	right: 0;
	font-size: 20px;
}
.icon-edit[data-v-47d164ca]:hover,
.icon-delete[data-v-47d164ca]:hover {
	color: #fa0;
}
/* 1.4 展开、收起地址卡片*/
.more-address[data-v-47d164ca] {
	text-align: center;
}
.more-address a[data-v-47d164ca]{
	font-size: 16px;
	color: #fa0;
}
/* 2.配送方式*/
#shipping > ul[data-v-47d164ca] {
	text-align: center;
}
#shipping > ul > li[data-v-47d164ca] {
	display: inline-block;
	width: 250px;
	margin: 0 10px;
	border: 1px dashed #bbb;
	line-height: 2;
	cursor: pointer;
}
